import {row, col, css} from '../utils.js'

// –∫–ª–∞—Å—Å –±–∞–∑–æ–≤–æ–≥–æ –±–ª–æ–∫–∞
class Block {
	constructor(value, options) {
		this.value = value
		this.options = options
	}

	// —Å–æ–∑–¥–∞—Å—Ç –æ—à–∏–±–∫—É –µ—Å–ª–∏ –≤ –∏–Ω—Å—Ç–∞–Ω—Å–µ –Ω–µ –æ–ø—Ä–µ–¥–µ–ª—ë–Ω —ç—Ç–æ—Ç –º–µ—Ç–æ–¥
	toHTML() {
		throw new Error('–ú–µ—Ç–æ–¥ toHTML –Ω–µ –æ–ø—Ä–µ–¥–µ–ª—ë–Ω')
	}
}

// –∫–ª–∞—Å—Å –∑–∞–≥–æ–ª–æ–≤–∫–∞ —É–Ω–∞—Å–ª–µ–¥–æ–≤–∞–Ω–Ω—ã–π –æ—Ç –∫–ª–∞—Å—Å–∞ Block
export class TitleBlock extends Block {
	constructor (value, options) {
		// –≤—ã–∑—ã–≤–∞–µ—Ç –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä –∫–ª–∞—Å—Å–∞ Block
		super(value, options)
	}

	// –≥–µ–Ω–µ—Ä–∞—Ü–∏—è html –ó–∞–≥–æ–ª–æ–≤–∫–∞
	toHTML() {
		const { tag = 'h1', styles } = this.options
		return row(col(`<${tag}>${this.value}</${tag}>`), css(styles))
	}
}

// –∫–ª–∞—Å—Å –∫–∞—Ä—Ç–∏–Ω–∫–∏ —É–Ω–∞—Å–ª–µ–¥–æ–≤–∞–Ω–Ω—ã–π –æ—Ç –∫–ª–∞—Å—Å–∞ Block
export class ImageBlock extends Block {
	constructor(value, options) {
		super(value, options)
	}

	// –≥–µ–Ω–µ—Ä–∞—Ü–∏—è html –ö–∞—Ä—Ç–∏–Ω–∫–∏
	toHTML() {
		const { imageStyles: is, alt = '', styles } = this.options
		return row(`<img src="${this.value}" alt="${alt}" style="${css(is)}" />`, css(styles))
	}
}

// –∫–ª–∞—Å—Å –∫–æ–ª–æ–Ω–æ–∫ —É–Ω–∞—Å–ª–µ–¥–æ–≤–∞–Ω–Ω—ã–π –æ—Ç –∫–ª–∞—Å—Å–∞ Block
export class ColumnsBlock extends Block {
	constructor(value, options) {
		super(value, options)
	}

	// –≥–µ–Ω–µ—Ä–∞—Ü–∏—è html –ö–æ–ª–æ–Ω–æ–∫
	toHTML() {
		const html = this.value.map(col).join('')
		// col –∫–∞–∂–¥—ã–π —Ä–∞–∑ –±—É–¥–µ—Ç –≤—ã–∑—ã–≤–∞—Ç—å—Å—è —Å –Ω–æ–≤—ã–º –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–º
	
		return row(html, css(this.options.styles))
		// –≤—ã–≤–æ–¥ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –º–∞—Å—Å–∏–≤–∞ –≤ —Å—Ç—Ä–æ–∫—É —Ä–∞–∑–¥–µ–ª—è–µ—Ç—Å—è –∑–∞–ø—è—Ç—ã–º–∏
		// .join('') —ç—Ç–æ —É—Å—Ç—Ä–∞–Ω—è–µ—Ç
	}
}

// –∫–ª–∞—Å—Å –ø–∞—Ä–∞–≥—Ä–∞—Ñ–∞ —É–Ω–∞—Å–ª–µ–¥–æ–≤–∞–Ω–Ω—ã–π –æ—Ç –∫–ª–∞—Å—Å–∞ Block
export class TextBlock extends Block {
	constructor(value, options) {
		super(value, options)
	}

	// –≥–µ–Ω–µ—Ä–∞—Ü–∏—è html –ü–∞—Ä–∞–≥—Ä–∞—Ñ–∞
	toHTML() {
		return row(col(`<p>
			${this.value}
		</p>`), css(this.options.styles))
	}
}